import React, { useState, useEffect } from "react";
import TextField from "@material-ui/core/TextField";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogContentText from "@material-ui/core/DialogContentText";
import DialogTitle from "@material-ui/core/DialogTitle";
import Button from "components/CustomButtons/Button.jsx";
import MediaQuery from "react-responsive";
import "./AddTask.css";
import Add from "@material-ui/icons/Add";
import useInputValue from "../../hooks/useInputValue"; //hook

function getToday() {
  let today = new Date();
  let dd = today.getDate();
  let mm = today.getMonth() + 1; //January is 0!
  let yyyy = today.getFullYear();

  if (dd < 10) {
    dd = "0" + dd;
  }

  if (mm < 10) {
    mm = "0" + mm;
  }

  return (today = yyyy + "-" + mm + "-" + dd);
}

export default function AddTask(props) {
  const [model, setModel] = useState(false);
  const { resetValue, ...task } = useInputValue("");
  const { resetValue: trackTaskResetValue, ...trackTask } = useInputValue("");
  const { resetValue: notesReetValue, ...notes } = useInputValue("");
  const { resetValue: notNeeded, ...plannedDate } = useInputValue(getToday());
  const currentTaskSchema = {
    description: "",
    owner: "tushar",
    priority: "",
    status: "",
    plannedDate: "",
    notes: ""
  };
  if (props.track) {
    currentTaskSchema.user = "";
  }
  const [currentTask, setCurrentTask] = useState(currentTaskSchema);
  const [taskValid, settaskValid] = useState(false);

  useEffect(
    () => {
      let validData = true;
      for (let i in currentTask) {
        if (!currentTask[i]) {
          validData = false;
        }
      }

      settaskValid(validData);
    },
    [currentTask]
  );
  useEffect(
    () => {
      setCurrentTask({
        ...currentTask,
        notes: notes.value
      });
    },
    [notes.value]
  );
  useEffect(
    () => {
      console.log("test");
      setCurrentTask({
        ...currentTask,
        plannedDate: plannedDate.value
      });
    },
    [plannedDate.value]
  );

  const addTask = () => {
    setCurrentTask({
      ...currentTask,
      description: task.value
    });
    resetValue();
    setModel(true);
  };
  const handleClose = () => {
    setModel(false);
  };
  const handleNewTask = () => {
    console.log(currentTask);
    props.addTask(currentTask);
    setModel(false);
  };

  return (
    <div className="wrapper">
      <Button
        round
        justIcon
        disabled={!task.value}
        onClick={addTask}
        variant="contained"
        color="primary"
      >
        <Add />
      </Button>
      <MediaQuery query="(min-device-width: 1224px)">
        <form
          onSubmit={e => {
            e.preventDefault();
            addTask();
          }}
          style={{ display: "inline-block", margin: 10, width: "80%" }}
        >
          <TextField
            fullWidth
            name="task"
            {...task}
            className="add_task"
            helperText="Type what needs to be done"
            label="Enter task name"
          />
        </form>
      </MediaQuery>
      <MediaQuery query="(max-device-width: 1224px)">
        <form
          onSubmit={e => {
            e.preventDefault();
            addTask();
          }}
          style={{ display: "inline-block", margin: 10 }}
        >
          <TextField
            fullWidth
            name="task"
            {...task}
            className="add_task"
            helperText="Type what needs to be done"
            label="Enter task name"
          />
        </form>
      </MediaQuery>
      <MediaQuery query="(min-device-width: 1224px)">
        <Dialog
          fullWidth
          open={model}
          onClose={handleClose}
          aria-labelledby="form-dialog-title"
        >
          <DialogTitle id="form-dialog-title">Fill task Details</DialogTitle>

          <DialogContent style={{ textAlign: "center" }}>
            <DialogContentText style={{ padding: 10 }}>
              Select <b>Priority</b> for current task.
            </DialogContentText>
            <select
              style={{ padding: 5 }}
              onChange={e =>
                setCurrentTask({ ...currentTask, priority: e.target.value })
              }
            >
              <option value="high">high</option>
              <option value="low">low</option>
              <option value="meduim">medium</option>
            </select>
            <DialogContentText style={{ padding: 10 }}>
              Select <b>Status</b> for current task.
            </DialogContentText>
            <select
              style={{ padding: 5 }}
              onChange={e =>
                setCurrentTask({ ...currentTask, status: e.target.value })
              }
            >
              <option value="planned">planned</option>
              <option value="unplanned">unplanned</option>
              <option value="completed">completed</option>
            </select>
            <br />
            <br />
            <TextField
              id="date"
              label="Planned Date"
              type="date"
              {...plannedDate}
            />
            <TextField
              {...notes}
              fullWidth
              margin="normal"
              label="Additional notes"
              helperText="notes"
            />
          </DialogContent>
          <DialogActions>
            <Button onClick={handleClose} color="primary">
              Cancel
            </Button>
            <Button
              onClick={handleNewTask}
              disabled={!taskValid}
              color="primary"
            >
              ADD TASK
            </Button>
          </DialogActions>
        </Dialog>
      </MediaQuery>
      <MediaQuery query="(max-device-width: 1224px)">
        <Dialog
          fullScreen
          fullWidth
          open={model}
          onClose={handleClose}
          aria-labelledby="form-dialog-title"
        >
          <DialogTitle id="form-dialog-title">Fill task Details</DialogTitle>

          <DialogContent style={{ textAlign: "center" }}>
            <DialogContentText style={{ padding: 10 }}>
              Select <b>Priority</b> for current task.
            </DialogContentText>
            <select
              style={{ padding: 5 }}
              onChange={e =>
                setCurrentTask({ ...currentTask, priority: e.target.value })
              }
            >
              <option value="high">high</option>
              <option value="low">low</option>
              <option value="meduim">medium</option>
            </select>
            <DialogContentText style={{ padding: 10 }}>
              Select <b>Status</b> for current task.
            </DialogContentText>
            <select
              style={{ padding: 5 }}
              onChange={e =>
                setCurrentTask({ ...currentTask, status: e.target.value })
              }
            >
              <option value="planned">planned</option>
              <option value="unplanned">unplanned</option>
              <option value="completed">completed</option>
            </select>
            <br />
            <br />
            <TextField
              id="date"
              label="Planned Date"
              type="date"
              {...plannedDate}
            />
            <TextField
              {...notes}
              fullWidth
              margin="normal"
              label="Additional notes"
              helperText="notes"
            />
          </DialogContent>
          <DialogActions>
            <Button onClick={handleClose} color="primary">
              Cancel
            </Button>
            <Button
              disabled={!taskValid}
              onClick={handleNewTask}
              color="primary"
            >
              ADD TASK
            </Button>
          </DialogActions>
        </Dialog>
      </MediaQuery>
    </div>
  );
}



